.filledInExercise--container{
	&__title{
		font-weight: bold;
		text-decoration: underline;
		margin-bottom: 15px;
	}

	&__diftitle{
		font-weight: bold;
		text-decoration: underline;
		margin: 15px 0 ;
	}

	&__card{
		background-color: $color-white;
		border-radius: 15px;
		margin: 15px 0;
		box-shadow: -2px 3px 3px #00000070;
		text-transform: capitalize;
		padding: 15px;
	}

}